.Empty {
  width: 80%;
  margin: 2% auto;
  text-align: center;
}

.Burger {
  width: 100%;
  margin: 20px auto 0px auto;
  height: 275px;
  overflow: auto;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

@media (min-width: 500px) and (min-height: 400px) {
  .Burger {
    width: 350px;
    height: 275px;
  }
}

@media (min-width: 500px) and (min-height: 401px) {
  .Burger {
    width: 450px;
    height: 275px;
  }
}
